
.comments-wrap {
	margin-top: 25px;
}
.comments-wrap .title {
	padding: 10px;
	font-size: 16px;
	font-weight: bold;
    background-color: #eaeaea;
    border-radius: 3px 3px 0 0;
}
.comments-item {
	padding: 25px 0;
	border-bottom: 1px solid #ccc;
}
.comments-item:first-child {
	padding: 0 0 20px 0;
}
.comments-item:last-child {
	border-bottom: none;
}
.comments-item .top {
	margin-bottom: 15px;
	color: #caa;
}
.comments-item .top>a {
	font-weight: bolder;
	font-size: 1em;
	padding-left: 24px;
	background: url(../../assets/icon/user.png) no-repeat top left;
}



