
ul.item-list {
  overflow: auto;
  margin: 0;
  padding: 0;
}
ul.item-list li {
  width: 261px;
  height: 270px;
  list-style: none;
  float: left;
  margin-right: 12px;
  margin-bottom: 16px;
  background: #fff;
}
ul.item-list li:nth-child(4n) {
  margin-right: 0;
}
.item-card .img-cover {
  width: 100%;
  height: 175px;
  text-align: center;
  overflow: hidden;
}
.item-card .img-cover > img {
  width: 100%;
  height: 100%;
  -webkit-transition: 1s;
  transition: 1s;
  background-color: #eee;
}
.item-card .img-cover > img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.item-card .item-meta {
  padding: 0 10px;
  text-align: right;
  color: #999;
}
.item-card .item-meta time {
  float: left;
  padding-left: 24px;
  background: url(../../assets/icon/time.png) no-repeat top left;
}
.item-card .item-meta .like {
  padding-left: 24px;
  background: url(../../assets/icon/like.png) no-repeat top left;
}
.item-card .item-meta .comment {
  margin-left: 10px;
  padding-left: 24px;
  background: url(../../assets/icon/comment.png) no-repeat top left;
}
.item-card h4 {
  padding: 10px;
  margin: 0;
  font-weight: 400;
  height: 45px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 1.0rem;
  line-height: 26px;
}



