
.hero {
  background-image: url(http://i1.piimg.com/575157/9fde24e6975dec11.jpg);
  /*margin-top: -50px;*/
  position: relative;
  z-index: 0;
  margin: 0;
  padding-top: 50px;
  background-size: cover;
  background-position: 100% 50%;
  text-align: center;
  color: #fff;
  background-color: #555;
}
.hero .h-container {
  position: relative;
  padding: 80px 0 80px;
}
.h-container {
  padding-left: 15px;
  padding-right: 15px;
}
.label-list span {
  margin-right: 10px;
  cursor: pointer;
  font-weight: bold;
  font-size: 19px;
}
.label-list span:hover {
  color: #fcc;
}
.sub-title {
  margin-bottom: 40px;
}



